import React, { Component } from "react";
import Card from "react-md/lib/Cards/Card";
import Helmet from "react-helmet";
import { Link } from "gatsby";
import Layout from "../layout";
import config from "../../data/SiteConfig";
import "./Main.scss";

class MainPage extends Component {
  render() {
    return (
      <div className='about-container'>
        <Helmet>
          <title>{`main | ${config.siteTitle}`}</title>
          <link rel='canonical' href={`${config.siteUrl}`} />
        </Helmet>
        <div className='md-grid md-cell--8'>
          <div className='logo'>
            <img
              src={`${
                config.siteUrl
              }/Emoticon3D-Material/assets/mainicon_256x256.png`}
              className='smallbuttonIcon'
              alt='icon'
            />
            <h3 className='smallbuttonText'>3D요</h3>
          </div>
          <div className='main-wrapper'>
            <div className='sub-wrapper'>
              <div className='subsub-wrapper'>
                <Link to='/search'>
                  <img
                    src={`${
                      config.siteUrl
                    }/Emoticon3D-Material/assets/main_256x256.png`}
                    alt='icon'
                    className='mainIconButton'
                  />
                  <p>모델검색</p>
                </Link>
              </div>
              <div className='subsub-wrapper'>
                <Link to='/reserv'>
                  <img
                    src={`${
                      config.siteUrl
                    }/Emoticon3D-Material/assets/main2_256x256.png`}
                    alt='icon'
                    className='mainIconButton'
                  />
                  <p>예약하기</p>
                </Link>
              </div>
            </div>
            <div className='sub-wrapper'>
              <div className='subsub-wrapper'>
                <Link to='/check_print'>
                  <img
                    src={`${
                      config.siteUrl
                    }/Emoticon3D-Material/assets/main3_256x256.png`}
                    alt='icon'
                    className='mainIconButton'
                  />
                  <p>출력상태</p>
                </Link>
              </div>
              <div className='subsub-wrapper'>
                <Link to='/more'>
                  <img
                    src={`${
                      config.siteUrl
                    }/Emoticon3D-Material/assets/Thumbnail1_256x256.png`}
                    alt='icon'
                    className='mainIconButton'
                  />
                  <p>내3D모델</p>
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default MainPage;
